body {
  width: 100vw;
  height: 100vh;
}

main {
    width: 100%;
    border: 2px solid azure;
    width: 90%;
    height: 99vh;
    position: absolute; /* or absolute */
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

* {
    text-align: center;
    color: azure;
    font-family: "TajamukaScript"; 
}



