.HowToPlay {
    width: 100%;
    text-align: center;
    color: azure;
    font-family: "TajamukaScript"; 
    padding: 25px;
    margin-top: 50px;
}

.back {
    position: absolute;
    bottom: 10px;
    right: 20px;
    font-size: 30px;
    cursor: pointer;
    text-decoration: none;
    color: #ffffff;
}

.back:hover {
    color: crimson;
}

h1 {
    font-size: 50px;
    letter-spacing: 10px;
}

p, ol {
    letter-spacing: 5px;
    font-size: 30px;
}

ol li {
    margin-bottom: 10px;
}

ol {
    text-align: start;
}

@font-face {
    font-family: "TajamukaScript"; 
    src: url("../fonts/Tajamuka\ Script.ttf") format("truetype"); 
    font-style: normal; 
    font-weight: normal; 
} 



