.btnStartGame, 
.btnHowToPlay,
.btnTop10Players {
    background-color: azure;
    outline: none;
    border: none;
    border-radius: 45%;
    position: fixed; /* or absolute */
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: black;
    font-family: "TajamukaScript"; 
    letter-spacing: 10px;
}

.btnStartGame:hover,
.btnHowToPlay:hover,
.btnTop10Players:hover {
    background-color: aliceblue;
    cursor: pointer;
    color: crimson;
}

.btnStartGame {
    width: 600px;
    height: 150px;
    top: 24%;
    font-size: 50px;
}

.btnHowToPlay, 
.btnTop10Players {
    width: 500px;
    height: 100px;
    font-size: 30px;
}

.btnHowToPlay {
    top: 54%;
}

.btnTop10Players {
    top: 79%;
}

@font-face {
    font-family: "TajamukaScript"; 
    src: url("../fonts/Tajamuka\ Script.ttf") format("truetype"); 
    font-style: normal; 
    font-weight: normal; 
} 



